n\t\tcolor: var(--color-primary-element);\n\t\twidth: var(--icon-size);\n\t\theight: var(--icon-size);\n\t}\n\n\t&--disabled &__label {\n\t\topacity: $opacity_disabled;\n\t\t.checkbox-radio-switch__icon {\n\t\t\tcolor: var(--color-main-text)\n\t\t}\n\t}\n\n\t&:not(&--disabled) &__label:hover,\n\t&:not(&--disabled) &__label:focus-within {\n\t\tbackground-color: var(--color-primary-light);\n\t}\n\n\t// Switch specific rules\n\t&-switch:not(&--checked) &__icon {\n\t\tcolor: var(--color-text-maxcontrast);\n\t}\n\n\t// If switch is checked AND disabled, use the fade primary colour\n\t&-switch.checkbox-radio-switch--disabled.checkbox-radio-switch--checked &__icon {\n\t\tcolor: var(--color-primary-element-light);\n\t}\n\n\t&--button-variant &__label {\n\t\tborder-radius: 0;\n\t\twidth: 100%;\n\t\tmargin: 0;\n\t}\n\n\t&--button-variant:not(&--button-variant-v-grouped):not(&--button-variant-h-grouped) {\n\t\tborder-radius: var(--border-radius-large);\n\t}\n\n\t&--button-variant-v-grouped {\n\t\t&:first-of-type {\n\t\t\tborder-top-left-radius: var(--border-radius-large);\n\t\t\tborder-top-right-radius: var(--border-radius-large);\n\t\t}\n\t\t&:last-of-type {\n\t\t\tborder-bottom-left-radius: var(--border-radius-large);\n\t\t\tborder-bottom-right-radius: var(--border-radius-large);\n\t\t}\n\n\t\t// avoid double borders between elements\n\t\t& + &:not(&.checkbox-radio-switch--checked) {\n\t\t\tborder-top: 0;\n\t\t}\n\t\t& + &.checkbox-radio-switch--checked {\n\t\t\t// as the selected element has all borders:\n\t\t\t// small trick to cover the previous bottom border (only if there is one)\n\t\t\tmargin-top: -2px;\n\t\t}\n\t}\n\n\t&--button-variant-h-grouped {\n\t\t&:first-of-type {\n\t\t\tborder-top-left-radius: var(--border-radius-large);\n\t\t\tborder-bottom-left-radius: var(--border-radius-large);\n\t\t}\n\t\t&:last-of-type {\n\t\t\tborder-top-right-radius: var(--border-radius-large);\n\t\t\tborder-bottom-right-radius: var(--border-radius-large);\n\t\t}\n\n\t\t// avoid double borders between elements\n\t\t& + &:not(&.checkbox-radio-switch--checked) {\n\t\t\tborder-left: 0;\n\t\t}\n\t\t& + &.checkbox-radio-switch--checked {\n\t\t\t// as the selected element has all borders:\n\t\t\t// small trick to cover the previous bottom border (only if there is one)\n\t\t\tmargin-left: -2px;\n\t\t}\n\t}\n\n\t&--button-variant.checkbox-radio-switch {\n\t\tborder: 2px solid var(--color-border-dark);\n\t\t// better than setting border-radius on labels (producing a small gap)\n\t\toverflow: hidden;\n\n\t\t&--checked {\n\t\t\tfont-weight: bold;\n\t\t\tborder: 2px solid var(--color-primary-element-light);\n\n\t\t\t&:hover {\n\t\t\t\tborder: 2px solid var(--color-primary);\n\t\t\t}\n\n\t\t\tlabel {\n\t\t\t\tbackground-color: var(--color-background-dark);\n\t\t\t}\n\t\t}\n\t}\n}\n","/**\n * @copyright Copyright (c) 2019 John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @author John Molakvoæ <skjnldsv@protonmail.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n// https://uxplanet.org/7-rules-for-mobile-ui-button-design-e9cf2ea54556\n// recommended is 48px\n// 44px is what we choose and have very good visual-to-usability ratio\n$clickable-area: 44px;\n\n// background icon size\n// also used for the scss icon font\n$icon-size: 16px;\n\n// icon padding for a $clickable-area width and a $icon-size icon\n// ( 44px - 16px ) / 2\n$icon-margin: math.div($clickable-area - $icon-size, 2);\n\n// transparency background for icons\n$icon-focus-bg: rgba(127, 127, 127, .25);\n\n// popovermenu arrow width from the triangle center\n$arrow-width: 9px;\n\n// opacities\n$opacity_disabled: .5;\n$opacity_normal: .7;\n$opacity_full: 1;\n\n// menu round background hover feedback\n// good looking on dark AND white bg\n$action-background-hover: rgba(127, 127, 127, .25);\n\n// various structure data used in the \n// `AppNavigation` component\n$header-height: 50px;\n$navigation-width: 300px;\n\n// mobile breakpoint\n$breakpoint-mobile: 1024px;\n\n// top-bar spacing\n$topbar-margin: 4px;\n\n// navigation spacing\n$app-navigation-settings-margin: 3px;\n"],"sourceRoot":""}]);
