<docs>

### General description

This component allows the user to choose a color. It consists of 2
actual pickers:

- One simple picker with a predefined palette of colors;
- One more advanced picker that provides the full color spectrum;

### Usage

* Using v-model and passing in an HTML element that will be treated as a trigger:

```vue
<template>
	<div class="container0">
		<NcColorPicker v-model="color">
			<NcButton> Click Me </NcButton>
		</NcColorPicker>
		<NcColorPicker v-model="color" :palette="customPalette">
			<NcButton> Click Me for a custom palette </NcButton>
		</NcColorPicker>
		<div :style="{'background-color': color}" class="color0" />
	</div>
</template>
<script>
export default {
	data() {
		return {
			color: '#0082c9',
			customPalette: [
				'#E40303',
				'#FF8C00',
				'#FFED00',
				'#008026',
				'#24408E',
				'#732982',
				'#5BCEFA',
				'#F5A9B8',
				'#FFFFFF',
				'#F5A9B8',
				'#5BCEFA',
			],
		}
	}
}
</script>
<style>
.container0 {
	display: flex;
	gap: 20px;
}

.color0 {
	width: 100px;
	height: 40px;
	border-radius: 6px;
}
</style>
```

* Using v-bind for both color and open state and emitting an event that updates the color

```vue
<template>
	<div class="container1">
		<NcButton @click="open = !open"> Click Me </NcButton>
		<NcColorPicker :value="color" @input="updateColor" :shown.sync="open">
			<div :style="{'background-color': color}" class="color1" />
		</NcColorPicker>
	</div>
</template>
<script>
export default {
	data() {
		return {
			color: '#0082c9',
			open: false
		}
	},
	methods: {
		updateColor(e) {
			this.color = e
		}
	}
}
</script>
<style>
.container1 {
	display: flex;
}

.color1 {
	width: 100px;
	height: 40px;
	margin-left: 20px;
	border-radius: 6px;
}
</style>
```

* Using advanced fields including HEX, RGB, and HSL:

```vue
<template>
	<div class="container0">
		<NcColorPicker v-model="color"
			:advanced-fields="true">
			<NcButton> Click Me </NcButton>
		</NcColorPicker>
		<div :style="{'background-color': color}" class="color0" />
	</div>
</template>
<script>
export default {
	data() {
		return {
			color: '#0082c9'
		}
	}
}
</script>
<style>
.container0 {
	display: flex;
}

.color0 {
	width: 100px;
	height: 40px;
	margin-left: 20px;
	border-radius: 6px;
}
</style>
```
</docs>

<template>
	<NcPopover v-bind="$attrs" v-on="$listeners" @apply-hide="handleClose">
		<template #trigger>
			<slot />
		</template>
		<div class="color-picker"
			:class="{ 'color-picker--advanced-fields': advanced && advancedFields }">
			<transition name="slide" mode="out-in">
				<div v-if="!advanced" class="color-picker__simple">
					<button v-for="(color, index) in palette"
						:key="index"
						:style="{'background-color': color }"
						class="color-picker__simple-color-circle"
						:class="{ 'color-picker__simple-color-circle--active' : color === currentColor }"
						type="button"
						@click="pickColor(color)">
						<Check v-if="color === currentColor"
							:size="20" />
					</button>
				</div>
				<Chrome v-if="advanced"
					v-model="currentColor"
					class="color-picker__advanced"
					:disable-alpha="true"
					:disable-fields="!advancedFields"
					@input="pickColor" />
			</transition>
			<div class="color-picker__navigation">
				<NcButton v-if="advanced"
					type="tertiary"
					@click="handleBack">
					<template #icon>
						<ArrowLeft :size="20" />
					</template>
				</NcButton>
				<NcButton v-if="!advanced"
					type="tertiary"
					@click="handleMoreSettings">
					<template #icon>
						<DotsHorizontal :size="20" />
					</template>
				</NcButton>
				<NcButton v-if="advanced"
					type="primary"
					@click="handleConfirm">
					{{ t('Choose') }}
				</NcButton>
			</div>
		</div>
	</NcPopover>
</template>

<script>
import NcButton from '../NcButton/index.js'
import NcPopover from '../NcPopover/index.js'
import l10n from '../../mixins/l10n.js'
import GenColors from '../../utils/GenColors.js'

import ArrowLeft from 'vue-material-design-icons/ArrowLeft.vue'
import Check from 'vue-material-design-icons/Check.vue'
import DotsHorizontal from 'vue-material-design-icons/DotsHorizontal.vue'

import { Chrome } from 'vue-color'

const rgbToHex = function(color) {
	const hex = color.toString(16)
	return hex.length === 1 ? '0' + hex : hex
}

export default {
	name: 'NcColorPicker',

	components: {
		ArrowLeft,
		Check,
		Chrome,
		DotsHorizontal,
		NcButton,
		NcPopover,
	},

	mixins: [l10n],

	props: {
		/**
		 * A HEX color that represents the initial value of the picker
		 */
		value: {
			type: String,
			required: true,
		},

		/**
		 * Set to `true` to enable advanced fields including HEX, RGB, and HSL
		 */
		advancedFields: {
			type: Boolean,
			default: false,
		},

		/**
		 * Provide a custom array of hexadecimal colors to show
		 */
		palette: {
			type: Array,
			default: () => GenColors(4).map(color => {
				return '#' + rgbToHex(color.r) + rgbToHex(color.g) + rgbToHex(color.b)
			}),
			validator(palette) {
				return palette.every(color => /^#([a-f0-9]{3}|[a-f0-9]{6})$/i.test(color))
			},
		},
	},

	emits: [
		'submit',
		'close',
		'update:open',
		'update:value',
		'input',
	],

	data() {
		return {
			currentColor: this.value,
			advanced: false,
		}
	},

	watch: {
		value(color) {
			this.currentColor = color
		},
	},

	methods: {
		/**
		 * Submit a picked colour and close picker
		 */
		handleConfirm() {
			/**
			 * Emits a hexadecimal string e.g. '#ffffff'
			 */
			this.$emit('submit', this.currentColor)
			this.handleClose()

			this.advanced = false
		},
		handleClose() {
			/**
			 * Emitted after picker close
			 */
			this.$emit('close')
			this.$emit('update:open', false)
		},

		/**
		 * Inner navigations
		 */
		handleBack() {
			this.advanced = false
		},
		handleMoreSettings() {
			this.advanced = true
		},

		/**
		 * Pick a colour
		 *
		 * @param {string} color the picked color
		 */
		pickColor(color) {
			if (typeof color !== 'string') {
				color = this.currentColor.hex
			}
			this.currentColor = color

			/**
			 * Emits a hexadecimal string e.g. '#ffffff'
			 */
			this.$emit('update:value', color)

			/**
			 * Emits a hexadecimal string e.g. '#ffffff'
			 */
			this.$emit('input', color)

		},
	},
}

</script>

<style lang="scss" scoped>
